.UsersInfo{
    width: 100%;
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    color: rgb(104, 104, 104);
    margin: 0 0 50px 0;
}

.UsersInfo .users-header{
    padding: 5px 20px;
}

.UsersInfo button{
    border: none;
    padding: 5px;
}

.users-header .manage{
    padding: 10px 0 0 0;
    align-items: center;
}

.manage .registerUser{
    color: white;
    background-color: #6ac751;
}
.manage .registerUser:hover{
    cursor: pointer;
    background-color: #448f2f;
}
.manage .manageUser{
    color: white;
    background-color: #51a5c7;
}
.manage .manageUser:hover{
    cursor: pointer;
    background-color: #2b5e72;
}

.users-body{
    padding: 0 20px 20px 20px;
}

.users-header h3{
    margin-bottom: 0;
    color: #668fca;
}

.users-footer{
    padding: 20px;
}

.users-table  [data-testid="ModeEditIcon"]{
    color: #4ac529;
    border: 1px solid #4ac529;
    border-radius: 5px;
}

.users-table  [data-testid="ModeEditIcon"]:hover{
    background-color: #4ac529;
    color: white;
    cursor: pointer;
}
.users-table  [data-testid="FormatAlignJustifyIcon"]{
    color: #29c5bd;
    border: 1px solid #29c5bd;
    border-radius: 5px;
}

.users-table  [data-testid="FormatAlignJustifyIcon"]:hover{
    background-color: #29c5bd;
    color: white;
    cursor: pointer;
}
.users-table  [data-testid="SaveIcon"]{
    color: #296dc5;
    border: 1px solid #296dc5;
    border-radius: 5px;
}

.users-table  [data-testid="SaveIcon"]:hover{
    background-color: #296dc5;
    color: white;
    cursor: pointer;
}
.users-table  [data-testid="DoDisturbIcon"],
.users-table  [data-testid="DeleteIcon"],
.users-table  [data-testid="LockResetIcon"]{
    color: #c52e29;
    border: 1px solid #c52e29;
    border-radius: 5px;
}

.users-table  [data-testid="DoDisturbIcon"]:hover,
.users-table  [data-testid="DeleteIcon"]:hover,
.users-table  [data-testid="LockResetIcon"]:hover{
    background-color: #c52e29;
    color: white;
    cursor: pointer;
}

.users-table  [data-testid="ChangeCircleIcon"]{
    color: #7198a0;
    border: 1px solid #7198a0;
    border-radius: 5px;
}
.users-table  [data-testid="ChangeCircleIcon"]:hover{
    background-color: #7198a0;
    color: white;
    cursor: pointer;
}
.users-form{
    background-color: white;
    min-width: 350px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    z-index: 1001;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 57%), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: rgb(104, 104, 104);
    display: none;
}

.users-form .form-header{
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
}

.users-form .form-body{
    padding: 20px;
}

.form-header .title{
    width: 70%;
}
.form-header .close{
    width: 30%;
    text-align: right;
}

.form-header .close [data-testid="CloseIcon"]:hover{
    cursor: pointer;
}

.users-form .form-footer{
    align-items: center;
    justify-content: right;
    padding: 20px;
}

.form-footer button{
    border: none;
    height: 30px;
    color:white;
    background-color:#004c9c;
}
.form-footer button:hover{
    cursor: pointer;
    background-color:#01366e;
}

.user-template-body,
.reports-template-body{
    width: 1200px;
    padding: 0 !important;
}
.reports-template-body .container thead th{
    top: 0;
}
.user-temp-fixed-header {
    position: sticky;
    top: 0;
    left: 0;
    z-index: 1000;
    background-color: white;
    padding: 12px 20px; /* Adjust as needed */
}

.user-template-body .user-cont{
    max-height: 500px;
}

.user-template-body .container thead th{
    top: 136px;
}

.user-template-body .user-cont thead th{
    top: 0;
}

@media only screen and (max-width: 1300px){
    .user-template-body{
        width: 1000px;
    }
}

@media only screen and (max-width: 1200px){
    .user-template-body .user-cont{
        max-height: 350px;
    }
}

@media only screen and (max-width: 1150px){
    .user-template-body{
        width: 800px;
    }
}

@media only screen and (max-width: 900px){
    .user-template-body{
        width: 500px;
    }
}

@media only screen and (max-width: 900px){
    .user-template-body{
        width: 500px;
    }
}

@media only screen and (max-width: 600px){
    .user-template-body{
        width: 300px;
    }
}