
.calendar-buttons .command,
.calendar-buttons .next-prev{
    height: 30px;
    color: white;
    background-color: #668fca;
    border: 1px solid white;
    padding: 0 30px;
}
.calendar-buttons .current{
    height: 30px;
    color: white;
    background-color: #ff9029;
    border: 1px solid white;
    padding: 0 30px;
}

.current-month .dateHolder{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
}

.current-month .eventsHolder{
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    width: 100%;
}

.eventsHolder .announcementHolder{
    font-size: 12px;
    background-color: #e36c96;
    color: white;
    padding: 5px;
    margin: 5px;
    position: relative;
}
.eventsHolder .announcementHolder:hover{
    background-color: #ca4e7a;
    cursor: pointer;
}

.memoHolder .memoLength,
.announcementHolder .announcementLength{
    position: absolute;
    top: -10px;
    left: -5px;
    width: 15px;
    height: 15px;
    background-color: red;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 10px;
}



.eventsHolder .memoHolder{
    font-size: 12px;
    background-color: #7b92f1;
    color: white;
    padding: 5px;
    margin: 5px;
    position: relative;
}
.eventsHolder .memoHolder:hover{
    background-color: #576dc4;
    cursor: pointer;
}

.current-month:hover{
    cursor: pointer;
    background-color: #668fca;
    color: white;
}

.activeDate{
    background-color: #668fca !important;
    color: white !important;
}

.calendar-buttons .command{
    height: 40px;
    border-radius: 10px;
}

.calendar-buttons .memo{
    background-color: #7b92f1;
}
.calendar-buttons .memo:hover{
    background-color: #3757d8;
    cursor: pointer;
}
.calendar-buttons .announcement{
    background-color: #e36c96;
}
.calendar-buttons .announcement:hover{
    background-color: #e02768;
    cursor: pointer;
}
.calendar-buttons .viewAll{
    background-color:#45bdd0;
}
.calendar-buttons .viewAll:hover{
    background-color: #45bdd0;
    cursor: pointer;
}
.calendarManage [data-testid="DeleteForeverIcon"]{
    color:#ff7e7e;
    border-radius: 5px;
    border: 1px solid #ff7e7e;
}
.calendarManage [data-testid="DeleteForeverIcon"]:hover{
    background-color: #ff7e7e;
    color: white;
    cursor: pointer;
}


.calendar-buttons .next-prev:hover{
    cursor: pointer;
    background-color: #385d92;
}
.calendar-buttons .current:hover{
    cursor: pointer;
    background-color: #c77121;
}

.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 4px;
}

.calendar-cell {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    border: 1px solid #ccc;
}

.empty-cell {
    background-color: #f0f0f0;
}

.day-of-week {
    background-color: #e0e0e0;
    font-weight: bold;
}

.current-date {
    background-color: #ff9029;
    color: white;
}

.date-today{
    font-size: 36px;
    padding: 20px 0;
    color: #668fca;
}

.date-today .month-year:hover{
    cursor: pointer;
    text-decoration: underline;
}

.date-today .custom-input{
    border: none;
    background-color: transparent;
    color: #668fca;
    font-size: 36px;
}

.date-today .custom-input:hover{
    cursor: pointer;
    text-decoration: underline;
}

.date-today .react-datepicker{
    color: #668fca;
}

.date-today .react-datepicker-year-header{
    color: #668fca;
}

.date-today [tabindex="0"]{
    color: rgb(104, 104, 104);
}

.prev-month,
.next-month {
  opacity: 0.5; /* Adjust the opacity as needed */
}

.calendar-buttons{
    justify-content: end;
}

.event-toggleButton{
    background-color: #f6b8b8;
    color: white;
    margin: 1px;
    border-radius: 3px;
}

.event-toggleButton:hover{
    background-color: #cc9898;
    cursor: pointer;
}

.events-list{
    position: absolute;
    background: white;
    left: -100px;
    bottom: -70px;
    border: 1px solid #d6d6d6;
    padding: 5px;
    z-index: 1;
    min-width: 100px;
    min-height: 65px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media only screen and (max-width:1000px){
    .calendar-header{
        flex-direction: column;
    }

    .commandButtons{
        width: 100%;
        margin-top: 10px;
    }

    .calendar-buttons{
        justify-content: start;
    }

    .cmdButton{
        flex-wrap: wrap;
    }

    .date-today{
        flex-direction: column;
    }
}