@-webkit-keyframes slideLeft-sidebar {
    0% { transform: translateX(0%); }
    100% { transform: translateX(-100%); }
}

@keyframes slideLeft-sidebar {
    0% { transform: translateX(0%); }
    100% { transform: translateX(-100%); }
}

@-webkit-keyframes slideRight-sidebar {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(0%); }
}

@keyframes slideRight-sidebar {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(0%); }
}

.SidebarContainer {
    position: fixed;
    height: 100%;
    width: 300px;
    top: 0;
    left: 0;
    z-index: 3000;
    display: flex;
    flex-direction: row;
    pointer-events: none;
}

.SidebarHolder {
    height: 100%;
    background-color: white;
    overflow-y: auto;
    overflow-x: hidden;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2); */
    flex-grow: 1;
    position: relative;
    flex: 1;
    pointer-events: auto;
}

.sideBar-hidden{
    left: 0%;
    top: 0%;
    transform: translateX(0%);
    -webkit-animation: slideLeft-sidebar 0s forwards;
    -webkit-animation-delay: 0s;
    animation: slideLeft-sidebar 0.2s forwards;
    animation-delay: 0s;
    pointer-events: auto;
}
.sideBar-show{
    left: 0%;
    transform: translateX(-100%);
    -webkit-animation: slideRight-sidebar 0s forwards;
    -webkit-animation-delay: 0s;
    animation: slideRight-sidebar 0.2s forwards;
    animation-delay: 0s;
    pointer-events: auto;
}

.sideBarHide{
    position: absolute;
    height: 30px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #668fca;
    color: white;
    border-radius: 5px 0 0 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
    right: 0;
    pointer-events: auto;
}
.sideBarShow{
    position: absolute;
    height: 30px;
    width: 40px;
    display: flex;
    align-items: center;
    background-color: #668fca;
    color: white;
    z-index: -1;
    border-radius: 0 5px 5px 0;
    top: 35px;
    left: 0;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
    pointer-events: auto;
}
.sideBarHide:hover,
.sideBarShow:hover{
    cursor: pointer;
    background-color: #4b6c9b;
    transform: scale(1.2, 1.2);
}

.SidebarTitle{
    color: #668fca;
    width: 100%;
    height: 100px;
    list-style-type: none;
    justify-content: center;
    align-items: center;
    border-bottom: 3px solid #F1F2FF;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 1001;
}

.SidebarTitle:hover{
    cursor: pointer;
}

.SidebarTitle .icon{
    width: 30%;
    display: grid;
    place-items: center;
}

.SidebarTitle .icon img{
    width: auto;
    height: 80px;
}

.SidebarTitle .title{
    width: 55%;
    font-weight: bold;
}

.Sidebar{
    height: 100%;
    width: 200px;
    background-color: white;
    padding: 1%;
}

.SidebarMenu{
    margin: 20% 0% 0% 20%;
    color: gray;
    font-weight: bold;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    grid-template-rows: 16px 0;
    grid-gap: 22px;
}

.SidebarMenu:after, .SidebarMenu:before {
    content: " ";
    display: block;
    border-bottom: 1px solid #5a5a5a;
}

.SidebarList{
    height: auto;
    width: 100%;
    padding-bottom: 20px;
}

.SidebarList .SidebarRow{
    width: 100%;
    height: 60px;
    list-style-type: none;
    margin: 10px 0;
    color: gray;
    justify-content: center;
    align-items: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.SidebarList .SidebarRow:hover{
    cursor: pointer;
    color: #668fca;
    border-radius: 10px 10px 10px 10px;
    transform: scale(1.1);
}

.SidebarList #active{
    background-color: #F1F2FF;
    color: #668fca;
    border-radius: 10px 10px 10px 10px;
    transform: scale(1.1);
}

.SidebarRow #icon{
    flex: 30%;
    display: grid;
    place-items: center;
}

.SidebarRow #title{
    flex: 70%;
}

.mainte-subMenu{
    color: gray;
    display: none;
}
#mainte-subMenu-active{
    display: block;
}
.mainte-subMenu ul:hover{
    cursor: pointer;
    color: #668fca;
    border-radius: 10px 10px 10px 10px;
    transform: scale(1.1);
}

.mainte-subMenu ul{
    padding: 15px;
    margin-left: 30px;
    width: 80%;
}

#active-menu-dev,
#active-menu-loan,
#active-menu-govt,
#active-menu-payroll,
#active-menu-timeKeeping,
#active-menu-talentManagement,
#active-menu-personnel,
#active-menu-reports,
#active-menu-employeesTab,
#active-menu-transaction,
#active-menu-maintenance{
    cursor: pointer;
    color: #668fca;
    border-radius: 10px;
    transform: scale(1.1);
}

#active-subMenu-dev,
#active-subMenu-loan,
#active-subMenu-govt,
#active-subMenu-payroll,
#active-subMenu-timeKeeping,
#active-subMenu-talentManagement,
#active-subMenu-personnel,
#active-subMenu-reports,
#active-subMenu-employeesTab,
#active-subMenu-transaction,
#active-subMenu-maintenance{
    display: block;
}

#active-ul{
    background-color: #F1F2FF;
    color: #668fca;
    border-radius: 10px 10px 10px 10px;
    transform: scale(1.1);
}

.HamburgerMenu{
    display: none;
    pointer-events: auto;
}
.HamburgerMenu .icon{
    margin: 10px;
}

.SidebarTitle .closeMenu{
    width: 15%;
    color: gray;
    display: none;
}

@media(max-width: 900px){
    .SidebarHolder{
        transform: translateX(-100%);
    }

    .SidebarTitle{
        display: none;
    }

    .Sidebar{
        display: none;
    }

    #sidebar-active{
        display: block;
    }
    
    #sidebarTitle-active{
        display: flex;
    }

    #sidebarHolder-active{
        left: 0%;
        transform: translateX(-100%);
        -webkit-animation: slideRight-sidebar 0s forwards;
        -webkit-animation-delay: 0s;
        animation: slideRight-sidebar 0.2s forwards;
        animation-delay: 0s;
    }

    @-webkit-keyframes slideRight-sidebar {
        100% {  transform: translateX(0%) } 
    }

    @keyframes slideRight-sidebar {
        100% {  transform: translateX(0%) } 
    }

    #sidebarHolder-close {
        left: 0%;
        transform: translateX(0%);
        -webkit-animation: slideLeft-sidebar 0s forwards;
        -webkit-animation-delay: 0s;
        animation: slideLeft-sidebar 0.2s forwards;
        animation-delay: 0s;
    }

    @-webkit-keyframes slideLeft-sidebar {
        100% {  transform: translateX(-100%) } 
    }

    @keyframes slideLeft-sidebar {
        100% {  transform: translateX(-100%) } 
    }


    .HamburgerMenu{
        height: 40px;
        margin: 20px 10px 10px 0;
        display: flex;
        align-items: center;
        color: gray;
        z-index: 1000;
    }

    .HamburgerMenu:hover{
        cursor: pointer;
        color: #668fca;
    }

    #menuSlide {
        position: fixed;
    }

    #menuSlide-active {
        display: none;
    }

    .SidebarTitle .closeMenu{
        display: flex;
    }

    .SidebarTitle .closeMenu:hover{
        cursor: pointer;
        color: #668fca;
    }
}