.MainteInfo{
    width: 100%;
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    color: rgb(104, 104, 104);
    margin: 0 0 50px 0;
}

.MainteInfo .mainteHeader{
    padding: 5px 20px;
}

.mainte-body{
    padding: 10px 20px;
}

.mainte-body-top{
    padding: 10px 0;
}

.mainte-body-top .mainte-addBtn,
.mainte-body .mainte-search{
    width: 50%;
    align-items: center;
}

.mainte-search .mainte-search-input{
    width: 80%;
}

.mainte-search .mainte-search-input input{
    width: 100%;
    color: gray;
    height: 30px;
    border-radius: 3px;
    border: 1px solid #80808080;
    padding: 0 10px;
}

.mainte-body-top .mainte-addBtn{
    align-items: center;
    justify-content: end;
}

.mainte-body-top .mainte-addBtn button{
    align-items: center;
}

.mainte-body-top .mainte-addBtn .add{
    color: white;
    background-color: #4ac529;
    border: 1px solid #36961c;
    border-radius: 3px;
    height: 40px;
}

.mainte-body-top .mainte-addBtn .add:hover{
    cursor: pointer;
    background-color: #36961c;
}

