.maintenance-table table{
    font-size: 14px;
}

.maintenance-table table thead th{
    z-index: 2;
    background-color: #668fca;
    color: white
}

.maintenance-table  [data-testid="ModeEditIcon"]{
    color: #4ac529;
    border: 1px solid #4ac529;
    border-radius: 5px;
}

.maintenance-table  [data-testid="ModeEditIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #4ac529;
    cursor: pointer;
}
.maintenance-table  [data-testid="DeleteForeverIcon"]{
    color: #ff7e7e;
    border: 1px solid #ff7e7e;
    border-radius: 5px;
}

.maintenance-table  [data-testid="DeleteForeverIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #ff7e7e;
    cursor: pointer;
}

.sub-title{
    align-items: center;
    font-size: 13px
}

.mainteHeader h3{
    margin-bottom: 0;
    color: #668fca;
}

.filters select{
    width: 200px;
    color: rgb(104, 104, 104);
    height: 35px;
    border-radius: 3px;
    border-color: #80808080;
}

.logoImg {
    border-radius: 50%;
    width: auto;
    height: 50px;
}