.wrapper-holder {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: white;
  color: rgb(104, 104, 104);
  background: linear-gradient(to bottom, #76abff, #ffffff);
}

.wrapper {
  padding: 40px;
  border-radius: 10px;
  /* box-shadow: 0 4px 8px 0 rgb(0 0 0 / 57%), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
  background-color: white;
  background: linear-gradient(to bottom, white, #a0c7ff);
}

.wrapper form {
  width: 320px;
}

.wrapper .input-box {
  position: relative;
  width: 100%;
  height: 30px;
  margin: 30px 0;
}

.input-box input {
  width: 100%;
  height: 100%;
  background: transparent;
  border: none;
  outline: none;
  border: 1px solid rgb(102 143 202);
  color: rgb(104, 104, 104);
  border-radius: 5px;
  padding: 0 30px 0 10px;
}

.input-box .icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
}

.wrapper .remember-forgot {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
}

.remember-forgot a {
  text-decoration: none;
}

.remember-forgot a:hover {
  text-decoration: underline;
}

.wrapper .button-holder {
  padding: 10px;
}

.button-holder button {
  width: 100%;
  height: 45px;
  border: none;
  outline: none;
  border-radius: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.19);
  background-color: #2975c5;
  cursor: pointer;
  font-size: 16px;
  color: white;
  font-weight: 700;
}

.wrapper .register-link {
  font-size: 14px;
  text-align: center;
  margin-top: 20px;
}

.logo-login img {
  width: auto;
  height: 500px;
}

.adminMode-button {
  color: #668fca;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #668fca;
}

.adminMode-button:hover {
  cursor: pointer;
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slideOut {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

/* Apply the slide-in animation */
.slide-in {
  animation: slideIn 0.5s forwards;
}

/* Apply the slide-out animation */
.slide-out {
  animation: slideOut 0.5s forwards;
}

/* Form wrapper */
.form-wrapper {
  overflow: hidden;
  /* Ensure the content stays within bounds during animation */
  position: relative;
  width: 100%;
}

/* Forms styling */
.form-content {
  position: absolute;
  width: 100%;
  transition: transform 0.5s;
}

.hidden {
  display: none;
}


/* CODE + VERIFY ROW */
.code-row {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 30px 0;
}

/* FIX overlap */
.code-row .input-box {
  width: auto;
  /* override 100% */
  flex: 1;
  /* take remaining space only */
  margin: 0;
  padding-right: 8px;
}

.code-row .input-box input{
  width: 100%;
  padding-right: 0 !important;
}

/* remove default margin from input-box inside code row */
.input-box.no-margin {
  margin: 0;
  flex: 1;
}

.verify-btn {
  height: 30px;
  padding: 0 16px;
  border-radius: 5px;
  border: 1px solid rgb(102 143 202);
  background-color: #2975c5;
  color: white;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  white-space: nowrap;
}

.verify-btn:hover {
  background-color: #1f5fa5;
}

@media only screen and (max-width: 1000px) {
  .wrapper-holder {
    flex-direction: column;
    padding: 20px;
  }
}

@media only screen and (max-width: 500px) {
  .wrapper-holder {
    flex-direction: column;
  }

  .logo-login img {
    height: 300px;
  }

  .wrapper form {
    width: 200px;
  }
}