.container {
    /* max-height: 620px; */
    overflow: auto;
    text-align: center;
    max-width: 100% !important;
}

.container table {
    width: 100%;
    height: auto;
    /* box-shadow: 0 0 20px rgba(0, 0, 0, 0.1); */
}

.container th{
    padding: 15px;
    background-color: rgba(255, 255, 255, 0.2);
    color: white;
}

.container td {
    height: 30px;
    padding: 15px;
    background-color: rgba(255, 255, 255, 0.2);
    color: rgb(104, 104, 104);
    border: 1px solid #ddd;
}

.container tr{
    border-bottom: 1px solid rgb(161, 161, 161);
}

.container th {
    text-align: center;
}

.container thead th {
    background-color: #668fca;
    position: sticky;
    top: 0;
    z-index: 1;
    border: 1px solid #ddd;
}

.container tbody tr:hover {
    background-color: rgba(37, 37, 37, 0.08);
}

.container #tr-active {
    background-color: rgba(37, 37, 37, 0.08);
}

.container tbody td {
    z-index: 0;
    position: relative;
}

.avatarImg{
    border-radius: 50%;
    width: 50px;
    height: 50px;
}

.logo{
    border-radius: 50%;
    width: 50px;
    height: 50px;
}