.PayrollInfo{
    width: 100%;
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    color: rgb(104, 104, 104);
    margin: 0 0 50px 0;
}

.PayrollInfo .payrollHeader{
    padding: 0 20px;
    align-items: center;
    justify-content: space-between;
}

.PayrollInfo .payrollHeader button{
    background-color: #0062cc;
    color: white;
    border: none;
    border-radius: 3px;
    padding: 5px;
}

.PayrollInfo .payrollHeader button:hover{
    background-color: #004c9c;
    cursor: pointer;
}

.PayrollInfo .payroll-body{
    padding: 20px;
}

.payroll-title{
    align-items: center;
}

.PayrollInfo .payroll-body .payroll-table{
    display: none;
}

.PayrollInfo .payroll-body .payroll-table2 table,
.PayrollInfo .payroll-body .payroll-table table{
    font-size: 14px;
}

.PayrollInfo .payroll-body .payroll-table2 table thead th,
.PayrollInfo .payroll-body .payroll-table table thead th{
    z-index: 2;
    background-color: #668fca;
    color: white;
    min-width: 100px;
}
.filters-payroll{
    border: 1px solid #00000029; 
    border-radius: 5px; 
    padding: 10px;
}

.filter-item-payroll{
    width: 30%;
}

.payroll-filters{
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
}

.payroll-body .react-tabs__tab--selected {
    background-color: #668fca;
    color: white;
}

.payroll-body .react-tabs__tab-list{
    font-size: 14px;
}

.payroll-filters .payroll-filter-item{
    width: 30%;
}
.payroll-filters .payroll-filter-item .payroll-filter-item-name h5{
    margin: 0;
}

.payroll-filter-item-input .filterSelect{
    color: rgb(104, 104, 104);
    height: 40px;
    border-radius: 3px;
    border-color: #80808080;
    width: 100%;
}

.payroll-column{
    width: 100%;
}

.PayrollInfo .payroll-body .react-tabs{
    border: 1px solid #0000001a;
}

.payroll-setting{
    border: 1px solid rgba(104, 104, 104, 0.253);
    position: absolute;
    right: 80px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
    background-color: white;
    color: rgb(104, 104, 104);
    z-index: 5;
}

.payroll-setting .payroll-setting-item{
    padding: 10px;
    width: auto;
    text-align: left;
    align-items: center;
}

.payroll-setting .payroll-setting-item:hover{
    cursor: pointer;
    background-color: #c5c5c533;
}

.payroll-tab-header .payroll-tab-leftSide{
    overflow-x: auto;
    width: 50%;
}

.payroll-tab-header .payroll-tab-leftSide .leftSide-btn button{
    background-color: white;
    height: 40px;
    border: 1px solid #80808078;
    color: rgb(104, 104, 104);
}

.leftSide-btn #btn-tab-active{
    cursor: pointer;
    background-color: #e1b170;
    color: white;
}

.payroll-tab-header .payroll-tab-leftSide .leftSide-btn button:hover{
    cursor: pointer;
    background-color: #e1b170;
    color: white;
}

.filters-payroll .generate{
    background-color: #004c9c;
    color: white;
    border-radius: 3px;
    border: none;
    height: 35px;
}

.filters-payroll .generate:hover{
    background-color: #01346b;
    cursor: pointer;
}

.browse{
    border-radius: 3px;
    background-color: #668fca;
    color: white;
    padding: 5px 10px;
}
.browse:hover{
    cursor: pointer;
    background-color: #48668f;
}
.drop-files-here-hover{
    background-color: rgb(189, 189, 189);
    color: white;
}

.footer-payroll .post{
    background-color: #004c9c;
    color: white;
    border-radius: 3px;
    border: none;
    height: 40px;
}
.footer-payroll .post:hover{
    background-color: #01346b;
    cursor: pointer;
}
.payroll-tab-header .payroll-tab-leftSide .leftSide-btn .extract{
    color: white;
    background-color: #29aec5;
    align-items: center;
    border-radius: 3px;
}

.payroll-tab-header .payroll-tab-leftSide .leftSide-btn .extract:hover{
    cursor: pointer;
    background-color: #1f8597;
}

.payroll-tab-header .payroll-tab-rightSide .rightSide-btn .post{
    background-color: #004c9c;
    color: white;
    border-radius: 3px;
    border: none;
    height: 40px;
}

.payroll-tab-header .payroll-tab-rightSide .rightSide-btn .post:hover{
    background-color: #01346b;
    cursor: pointer;
}

.payroll-tab-header .payroll-tab-leftSide .leftSide-btn .add{
    color: white;
    background-color: #4ac529;
    border: 1px solid #36961c;
    align-items: center;
    border-radius: 3px;
}

.payroll-tab-header .payroll-tab-leftSide .leftSide-btn .add:hover{
    cursor: pointer;
    background-color: #276e13;
    border: 1px solid #36961c;
}

.payroll-tab-header .payroll-tab-leftSide .leftSide-btn .delete{
    color: white;
    background-color: #fa3a3a;
    border: 1px solid #c72525;
    align-items: center;
    border-radius: 3px;
}

.payroll-tab-header .payroll-tab-leftSide .leftSide-btn .delete:hover{
    cursor: pointer;
    background-color: #b92929;
    border: 1px solid #c72525;
}

.payroll-tab-header .payroll-tab-rightSide{
    width: 50%;
    align-items: center;
    justify-content: end;
}
.payroll-tab-header .payroll-tab-rightSide .rightSide-btn{
    padding: 0 1px;
}
.payroll-tab-header .payroll-tab-rightSide .rightSide-btn button{
    align-items: center;
    background-color: white;
    border-radius: 3px;
    height: 40px;
    border: 1px solid #80808078;
    color: rgb(104, 104, 104);
}

.payroll-tab-header .payroll-tab-rightSide .rightSide-btn .add{
    color: white;
    background-color: #4ac529;
    border: 1px solid #36961c;
}
.payroll-tab-header .payroll-tab-rightSide .rightSide-btn .add:hover{
    cursor: pointer;
    background-color: #36961c;
}
.payroll-tab-header .payroll-tab-rightSide .rightSide-btn .delete{
    color: white;
    background-color: #fa3a3a;
    border: 1px solid #c72525;
}
.payroll-tab-header .payroll-tab-rightSide .rightSide-btn .delete:hover{
    cursor: pointer;
    background-color: #c72525;
}

.payroll-table2  [data-testid="ModeEditIcon"],
.payroll-table  [data-testid="ModeEditIcon"]{
    color: #4ac529;
    border: 1px solid #4ac529;
    border-radius: 5px;
}
.payroll-table2  [data-testid="ModeEditIcon"]:hover,
.payroll-table  [data-testid="ModeEditIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #4ac529;
    cursor: pointer;
}
.payroll-table2  [data-testid="DeleteForeverIcon"],
.payroll-table  [data-testid="DeleteForeverIcon"]{
    color: #ff7e7e;
    border: 1px solid #ff7e7e;
    border-radius: 5px;
}
.payroll-table2  [data-testid="DeleteForeverIcon"]:hover,
.payroll-table  [data-testid="DeleteForeverIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #ff7e7e;
    cursor: pointer;
}

.select-employee-table  [data-testid="RemoveRedEyeIcon"]{
    color: #06a3be;
    border: 1px solid #06a3be;
    border-radius: 5px;
}

.select-employee-table  [data-testid="RemoveRedEyeIcon"]:hover{
    transform: scale(1.1);
    cursor: pointer;
    color: white;
    background-color: #06a3be;
}

.sync-adjustments{
    background-color: #004c9c;
    color: white;
    border-radius: 3px;
    border: none;
    height: 35px;
}
.sync-adjustments:hover{
    background-color: #013a77;
    cursor: pointer;
}

@media only screen and (max-width: 1000px){
    .payroll-tab-header{
        flex-direction: column;
        flex-flow: column-reverse;
    }

    .payroll-tab-header .payroll-tab-leftSide,
    .payroll-tab-header .payroll-tab-rightSide {
        width: 100%;
    }

    .payroll-tab-header .payroll-tab-rightSide{
        justify-content: start;
        padding: 10px 0;
    }

    .selectModal{
        width: 500px;
    }
}

@media only screen and (max-width: 900px){
    .payroll-item-holder{
        align-items: start !important;
    }

    .filter-item-payroll{
        width: 100%;
    }
}

@media only screen and (max-width: 700px){
    .selectModal{
        width: 300px;
    }
}