.TransactionInfo{
    width: 100%;
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    color: rgb(104, 104, 104);
    margin: 0 0 50px 0;
}

.TransactionInfo .transaction-header{
    padding: 5px 20px;
}

.transaction-body{
    padding: 10px 20px;
}

.transaction-body-top .transaction-addBtn,
.transaction-body .transaction-search{
    width: 50%;
    align-items: center;
}

.transaction-search-input{
    width: 80%;
}

.transaction-search .transaction-search-input input{
    width: 100%;
    color: gray;
    height: 30px;
    border-radius: 3px;
    border: 1px solid #80808080;
    padding: 0 10px;
}

.transaction-body-top .transaction-addBtn{
    align-items: center;
    justify-content: end;
}

.transaction-body-top .transaction-addBtn button{
    align-items: center;
}

.itinerary .transaction-addBtn .add,
.timeBtn .add,
.transaction-body-top .transaction-addBtn .add{
    color: white;
    background-color: #4ac529;
    border: 1px solid #36961c;
    border-radius: 3px;
    height: 40px;
}
.itinerary .transaction-addBtn .add:hover,
.timeBtn .add:hover,
.transaction-body-top .transaction-addBtn .add:hover{
    cursor: pointer;
    background-color: #36961c;
}
.transaction-body-top .transaction-addBtn .approve{
    color: white;
    background-color: #004c9c;
    border: 1px solid #023164;
    border-radius: 3px;
    height: 40px;
}
.transaction-body-top .transaction-addBtn .approve:hover{
    cursor: pointer;
    background-color: #023164;
}

.overtimelist .react-tabs{
    border: 1px solid #0000001a;
}

.transaction-table table{
    font-size: 14px;
}

.transaction-table .Approved{
    background-color: #226d0d;
    color: white;
}
.transaction-table .Disapproved{
    background-color: #9e2727;
    color: white;
}
.transaction-table .Cancelled{
    background-color: darkgray;
}

.mass-approval-btn .select-all{
    color: white;
    background-color: #06a3be;
    border: 1px solid #087588;
    border-radius: 3px;
    height: 40px;
}
.mass-approval-btn .select-all:hover{
    cursor: pointer;
    background-color: #087588;
}
.mass-approval-btn .approve{
    color: white;
    background-color: #004c9c;
    border: 1px solid #023164;
    border-radius: 3px;
    height: 40px;
}
.mass-approval-btn .approve:hover{
    cursor: pointer;
    background-color: #023164;
}
.mass-approval-btn .disapprove{
    color: white;
    background-color: #ce3434;
    border: 1px solid #a12929;
    border-radius: 3px;
    height: 40px;
}
.mass-approval-btn .disapprove:hover{
    cursor: pointer;
    background-color: #a12929;
}

.transaction-table table thead th{
    z-index: 2;
    background-color: #668fca;
    color: white
}

.transaction-table  .add{
    color: #004c9c;
    border: 1px solid #004c9c;
}
.transaction-table  .edit{
    color: #4ac529;
    border: 1px solid #4ac529;
}
.transaction-table  .recycle{
    color: #e49218;
    border: 1px solid #e49218;
}
.transaction-table  .delete{
    color: #ce3434;
    border: 1px solid #ce3434;
}
.transaction-table  .history{
    color: #209bbe;
    border: 1px solid #209bbe;
}
.transaction-table  .add:hover{
    background-color: #063669;
    color: white;
    cursor: pointer;
}
.transaction-table  .edit:hover{
    background-color: #226d0d;
    color: white;
    cursor: pointer;
}
.transaction-table  .recycle:hover{
    background-color: #f59300;
    color: white;
    cursor: pointer;
}
.transaction-table  .delete:hover{
    background-color: #681414;
    color: white;
    cursor: pointer;
}
.transaction-table  .history:hover{
    background-color: #186c83;
    color: white;
    cursor: pointer;
}


.top-filter{
    margin-top: 15px;
}

.top-filter .generate{
    background-color: #004c9c;
    color: white;
    border-radius: 3px;
    border: none;
    height: 35px;
}

.top-filter .generate:hover{
    background-color: #01346b;
    cursor: pointer;
}

.transaction-tr:nth-child(odd) {
    background-color: #b4b4b444;
}

.loan-tab .react-tabs__tab-list{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    overflow-x: auto;
    overflow-y: hidden;
    font-size: 14px;
}
.req-approval .react-tabs__tab-list{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    overflow-x: auto;
    overflow-y: hidden;
    font-size: 14px;
    background: #fff822;
}
.req-approval .react-tabs__tab{
    font-size: 20px;
    padding: 6px 20px;
}
.loan-tab .react-tabs__tab--selected{
    background-color: #668fca;
    color: white;
}
.transaction-table  .itinerary-manage [data-testid="DoDisturbIcon"],
.transaction-table  [data-testid="ThumbDownAltIcon"]{
    color: #c52929;
    border: 1px solid #c52929;
    border-radius: 5px;
}

.transaction-table .itinerary-manage [data-testid="DoDisturbIcon"]:hover,
.transaction-table  [data-testid="ThumbDownAltIcon"]:hover{
    transform: scale(1.1);
    cursor: pointer;
    color: white;
    background-color: #c52929;
}
.transaction-table  [data-testid="RemoveRedEyeIcon"]{
    color: #06a3be;
    border: 1px solid #06a3be;
    border-radius: 5px;
}

.transaction-table  [data-testid="RemoveRedEyeIcon"]:hover{
    transform: scale(1.1);
    cursor: pointer;
    color: white;
    background-color: #06a3be;
}
.transaction-table  [data-testid="ImageIcon"]{
    color: #06a3be;
    border: 1px solid #06a3be;
    border-radius: 5px;
}

.transaction-table  [data-testid="ImageIcon"]:hover{
    transform: scale(1.1);
    cursor: pointer;
    color: white;
    background-color: #06a3be;
}
.itineary-control  [data-testid="ListIcon"]{
    color: #06a3be;
    border: 1px solid #06a3be;
    border-radius: 5px;
}

.itineary-control  [data-testid="ListIcon"]:hover{
    transform: scale(1.1);
    cursor: pointer;
    color: white;
    background-color: #06a3be;
}
.transaction-table  .itinerary-manage [data-testid="SaveIcon"],
.transaction-table  [data-testid="ThumbUpIcon"]{
    color: #0631be;
    border: 1px solid #0631be;
    border-radius: 5px;
}

.transaction-table .itinerary-manage [data-testid="SaveIcon"]:hover,
.transaction-table  [data-testid="ThumbUpIcon"]:hover{
    transform: scale(1.1);
    cursor: pointer;
    color: white;
    background-color: #0631be;
}
.transaction-table  [data-testid="ModeEditIcon"]{
    color: #4ac529;
    border: 1px solid #4ac529;
    border-radius: 5px;
}

.transaction-table  [data-testid="ModeEditIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #4ac529;
    cursor: pointer;
}
.transaction-table  [data-testid="ChangeCircleIcon"]{
    color: #c58e29;
    border: 1px solid #c58e29;
    border-radius: 5px;
}

.transaction-table  [data-testid="ChangeCircleIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #c58e29;
    cursor: pointer;
}
.transaction-table  [data-testid="DeleteForeverIcon"]{
    color: #c52929;
    border: 1px solid #c52929;
    border-radius: 5px;
}

.transaction-table  [data-testid="DeleteForeverIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #c52929;
    cursor: pointer;
}

.sub-title{
    align-items: center;
    font-size: 13px
}

.transaction-header h3{
    margin-bottom: 0;
    color: #668fca;
}

.transaction-table tbody .active {
    background-color: #b1cdff;
    color: white;
}

.transaction-table tbody .active:hover {
    background-color: #b1cdff;
    color: white;
}

.update-days-range{
    background-color: #004c9c;
    color: white;
    height: 35px;
    border-radius: 3px;
}
.update-days-range:hover{
    background-color: #03366d;
    cursor: pointer;
}

.loan-button{
    align-items: center;
    height: 35px;
    margin: 0 1px;
    border-radius: 5px;
}

/* .Transaction-Form-Info{
    max-width: 700px;
} */

.loan-history-table .container thead th{
    top: 0 !important;
}

.transaction-form-item{
    width: 100%;
    align-items: center;
    padding: 10px 0 0 0;
}

.transaction-form-item .transaction-form-input{
    width: 100%;
}

.transaction-form-item .transaction-form-input input{
    width: 90%;
    height: 30px;
    border-radius: 3px;
    border: 1px solid #80808080;
    color: rgb(104, 104, 104);
}
.transaction-form-item .transaction-form-input select:focus,
.transaction-form-item .transaction-form-input input:focus{
    outline: 1px solid #668fca;
}
.transaction-form-item .transaction-form-input select{
    width: 90%;
    color: rgb(104, 104, 104);
    height: 30px;
    border-radius: 3px;
    border-color: #80808080;
}

.transaction-form-item .transaction-form-category{
    width: 170px;
}

.transaction-form-item .transaction-form-category h4{
    font-weight: normal;
}

.transaction-form-item .transaction-form-input .checkbox{
    width: 20px;
    height: 20px;
}

.transaction-form-item .transaction-form-input .checkbox:focus{
    outline: none;
}

.transaction-form-upper,
.transaction-form-footer{
    padding: 50px 0 0 0;
    display: flex;
    align-items: center;
    justify-content: right;
}

.transaction-form-footer .submit{
    background-color: #004c9c;
}
.transaction-form-footer .submit:hover{
    background-color: #082f5a;
    cursor: pointer;
}
.buttons .cancel{
    background-color: #ce3434;
    color: white;
    border: none;
    height: 34px;
    border-radius: 3px;
}
.buttons .cancel:hover{
    background-color: #9e2727;
    cursor: pointer;
}
.transaction-form-upper .viewHistory{
    background-color: #29aec5;
    margin-right: 5px;
}
.transaction-form-upper .viewHistory:hover{
    background-color: #1f8597;
    cursor: pointer;
}
.transaction-form-upper .upload{
    background-color: #4ac529;
    margin-right: 5px;
}
.transaction-form-upper .upload:hover{
    background-color: #43af25;
    cursor: pointer;
}

.transaction-form-upper button,
.transaction-form-footer button{
    height: 40px;
    color: white;
    border: none;
    border-radius: 5px;
}

.transaction-body .itinerary{
    /* border: 1px solid #c8c3c3; */
    margin: 20px 0;
    /* padding: 20px; */
}

.leave-balance-container{
    justify-content: end;
    border-radius: 5px;
    padding: 10px;
    background-color: #ed8b2c;
    color: white;
}

.search-icon{
    padding-top: 10px;
    text-align: center;
    color: gray;
}

.search-input{
    padding-top: 10px;
    width: 40%;
}

.search-input input{
    height: 100%;
    width: 100%;
    padding: 0;
    border: none;
    border-bottom: 1px solid;
    color: gray;
}

.search-input input:focus{
    outline: none;
}

input[type="radio"]:focus {
    outline: none;
}
.mobile-dtr-image{
    width: 200px;
    height: 200px;
}
.mobile-dtr-image:hover{
    cursor: pointer;
}

.generate-button-offset button{
    margin-top: 5px;
    color: white;
    background-color: #004c9c;
    border: none;
    border-radius: 3px;
    height: 35px;
}
.generate-button-offset button:hover{
    background-color: #082f5a;
    cursor: pointer;
}

.generate-button-offset{
    padding: 10px 0;
}

@media only screen and (max-width: 1100px){
    .transaction-from-splitItem{
        flex-direction: column;
    }
}
@media only screen and (max-width: 900px){
    .top-filter{
        margin-top: 0;
    }

    .transaction-form-item{
        flex-direction: column;
        align-items: start;
    }
}