.Topbar{
    height:70px;
    width: 100%;
    background-color: white;
    position: fixed;
    z-index: 2999;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2); */
}

.SearchBar{
    height: 50%;
    width: 35%;
    margin: 22px 0;
    margin-left: 320px;
    color: gray;
    outline-style: solid;
    outline-width: thin;
    outline-color: #BBBBBB;
    border-radius: 10px 10px 10px 10px;
    display: flex;
}

.SearchBar2{
    margin-left: 100px !important;
}

.SearchBar .Icon{
    padding: 5px;
    width: 10%;
    text-align: center;
}

.SearchBar .Input{
    padding: 5px;
    width: 90%;
}

.SearchBar .Input input{
    height: 100%;
    width: 100%;
    padding: 0;
    border: none;
    color: gray;
}

.SearchBar .Input input:focus{
    outline: none;
}

.topbar-searchList{
    background-color: white;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 57%), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: rgb(104, 104, 104);
    padding: 0;
    margin: 0;
    position: absolute;
    z-index: 5;
    left: 320px;
    top: 82.5%;
    width: 35%;
    max-height: 500px;
    overflow-y: auto;
}

.topbar-searchList li{
    padding: 10px;
    list-style: none;
}

.topbar-searchList li:hover{
    cursor: pointer;
    background-color: #668fca;
    color: white;
}

.topbar-searchList li.focused {
    background-color: #668fca;
    color: white;
}

.User{
    position: absolute;
    right: 5%;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}

.User .UserImg{
    height: 50px;
    width: 50px;
}

.User .UserImg img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1);
}
.User .UserImg img:hover{
    cursor: pointer;
}

.User .Btn{
    text-align: center;
    height: 30px;
    width: 30px;
    color: gray;
    margin-top: 10px;
}

.User .Btn:hover{
    cursor: pointer;
}

.User .ddContent{
    text-align: center;
    position: absolute;
    color: rgb(104, 104, 104);
    background-color: #ffffff;
    min-width: 160px;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    z-index: 5;
    padding: 10px;
    margin: 10px 0;
    right: 5%;
    top: 90%;
    border-radius: 10px 10px 10px 10px;
}

.User .ddContent-item{
    padding: 20px 0;
    border-bottom: 1px solid #d7d7d7;
}

.User .ddContent-item:hover{
    cursor: pointer;
    color:#668fca;
}

@media(max-width: 900px){
    .topbarToggle{
        position: fixed;
        right: 0;
        height: 40px;
        margin: 10px 0 10px 0;
        display: flex;
        align-items: center;
        background-color: white;
        color: gray;
    }

    .SearchBar{
        margin-left: 70px;
    }
    .topbar-searchList{
        left: 0;
        margin-left: 70px;
    }
}