.ReportsInfo{
    width: 100%;
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    color: rgb(104, 104, 104);
    margin: 0 0 50px 0;
}

.ReportsInfo .reports-header{
    padding: 5px 20px;
}

.reports-body{
    padding: 20px;
}

.reports-header h3{
    margin-bottom: 0;
    color: #668fca;
}

option{
    color: rgb(104, 104, 104);
}

.reports-footer{
    padding: 20px;
}

.reports-footer .buttons{
    align-items: center;
    justify-content: right;
}

.reports-footer button{
    width: 100px;
    height: 40px;
    border: none;
    border-radius: 5px;
}

.reports-footer .add{
    color: white;
    background-color: #4cc92a;
}

.reports-footer .add:hover{
    cursor: pointer;
    background-color: #36921d;
}

.reports-footer .generate{
    background-color: #004c9c;
    color: white;
}
.reports-footer .generate:hover{
    cursor: pointer;
    background-color: #05376b;
}

.payroll-history-table .view{
    color: white;
    background-color: #57b0a9;
    border-radius: 5px;
    width: 50px;
    height: 30px;
}

.payroll-history-table .view:hover{
    cursor: pointer;
    background-color: #377570;
}

.payroll-history-table [data-testid="RemoveRedEyeIcon"]{
    font-size: 16px;
}

.payslip-employee{
    width: 70%;
    height: 30px;
    border-radius: 3px;
    border: 1px solid #80808080;
    color: rgb(104, 104, 104);
}

.datePay-section .dateInput{
    width:70%;
    height:30px;
    border-radius:3px;
    border:1px solid #80808080;
    color: rgb(104, 104, 104);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.payslip-form{
    background-color: white;
    max-height: 800px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    z-index: 1001;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 57%), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: rgb(104, 104, 104);
    overflow-y: auto;
    overflow-wrap: break-word;
    display: none;
}

.payslip-form .numbers{
    width: 120px;
}

.payslip-form .payslipValues{
    border: none;
    color: rgb(104, 104, 104);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 15px;
    font-weight: bold;
}

.payslip-form .payslipValues:focus{
    outline:none;
}

.payslip-form .buttons{
    align-items: center;
    justify-content: end;
}

.payslip-form .buttons [data-testid="LocalPrintshopIcon"]{
    color: #377570;
}
.payslip-form .buttons [data-testid="LocalPrintshopIcon"]:hover{
    cursor: pointer;
    color: #187c74;
}
.payslip-form .buttons [data-testid="CloseIcon"]:hover{
    cursor: pointer;
}

.payslip-content{
    padding: 10px 20px;
}

.payslip-header{
    color: #668fca;
    padding: 20px 0;
}

.payslip-form .left{
    width: 50%;
}
.payslip-form .right{
    width: 50%;
}
.payslip-form label{
    color: #668fca;
    padding: 10px 0;
}

.report-item{
    width: 50% !important;
}

.payslip-body{
    width: 700px;
    padding: 20px 0;
}

@media only screen and (max-width: 900px){
    .payslip-employee{
        width: 100%;
    }

    .payslip-body{
        width: 500px;
    }
}

@media only screen and (max-width: 700px){
    .info-item{
        width: 100%;
    }

    .datePay-section .dateInput{
        width: 90%;
    }

    .payslip-body{
        width: 300px;
    }

    .payslip-sect{
        flex-direction: column;
    }

    .payslip-form .left,
    .payslip-form .right{
        width: 100%;
    }
}