.TimeInfo{
    height: auto;
    width: 100%;
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    color: rgb(104, 104, 104);
    margin: 0 0 50px 0;
}

.sortable-header{
    user-select: none;
}

.TimeInfo .timeHeader{
    padding: 0px 20px;
}

.TimeInfo .timeHeader button:hover {
    background-color: #004c9c;
    cursor: pointer;
}
.TimeInfo .timeHeader button {
    background-color: #0062cc;
    color: white;
    border: none;
    border-radius: 3px;
    padding: 5px;
}

.TimeInfo .timeHeader .tHeaderTitle{
    width: 50%;
    align-items: center;
    color: #668fca;
}

.TimeInfo .timeHeader .dateToday{
    width: 50%;
    align-items: center;
    justify-content: right;
}

.transaction-body-top{
    padding: 10px 0;
}

.TimeInfo .filters .filterHeader .filterHeader-left{
    width: 50%;
    color: #668fca;
}

.TimeInfo .filters .filterHeader .filterHeader-right{
    width: 50%;
    align-items: center;
    justify-content: right;
}

.filterHeader-right .filterInput input{
    width: 100%;
    color: gray;
    height: 20px;
    border-radius: 3px;
    border: 1px solid #80808080;
}

.filterHeader-right .filterInput input:focus{
    outline: none;
}

.TimeInfo .filters .filterName h5{
    margin: 0;
}

.TimeInfo .filters .filterItem{
    width: 100%;
    align-items: center;
    justify-content: space-between;
}

.TimeInfo .filters .filterItem .filter{
    width: 30%;
}

.TimeInfo .filters .filterItem .filterSelect{
    width: 100%;
    color:rgb(104, 104, 104);
    height: 40px;
    border-radius: 3px;
    border-color: #80808080;
}

.TimeInfo .mainData{
    padding: 20px;
}

.TimeInfo .timeBtns{
    align-items: center;
    justify-content: left;
    padding: 20px 0px;
}

.TimeInfo .timeBtns button{
    align-items: center;
    justify-content: center;
}

.TimeInfo .timeBtns .extract button{
    background-color: #29aec5;
    border: 1px solid #259db3;
    border-radius: 3px;
    padding: 10px;
}
.TimeInfo .timeBtns .extract button:hover{
    background-color: #155864;
    cursor: pointer;
}
.TimeInfo .timeBtns .delete button{
    background-color: #ff7e7e;
    border: 1px solid #ca6161;
    border-radius: 3px;
    padding: 10px;
}
.TimeInfo .timeBtns .delete button:hover{
    background-color: #ca6161;
    cursor: pointer;
}
.TimeInfo .timeBtns .download button{
    background-color: #2967c5;
    border: 1px solid #1f4c8f;
    border-radius: 3px;
    padding: 10px;
}
.TimeInfo .timeBtns .download button:hover{
    background-color: #1f4c8f;
    cursor: pointer;
}
.TimeInfo .timeBtns .upload button{
    background-color: #c57c29;
    border: 1px solid #a76a25;
    border-radius: 3px;
    padding: 10px;
}
.TimeInfo .timeBtns .upload button:hover{
    background-color: #a76a25;
    cursor: pointer;
}
.TimeInfo .timeBtns .filter-header button{
    background-color: #ef7dda;
    border: 1px solid #be5dad;
    border-radius: 3px;
    padding: 10px;
}
.TimeInfo .timeBtns .filter-header button:hover{
    background-color: #be5dad;
    cursor: pointer;
}

.timeBtns .post button{
    background-color: #004c9c;
    border: 1px solid #01346b;
    border-radius: 3px;
    padding: 10px;
}
.timeBtns .post button:hover{
    background-color: #01346b;
    cursor: pointer;
}

.TimeInfo .timeBtns .approve button{
    background-color: #0062cc;
    border: 1px solid #004c9c;
}
.TimeInfo .timeBtns .approve button:hover{
    background-color: #004c9c;
    cursor: pointer;
}
.TimeInfo .timeBtns .add button{
    background-color: #00cc55;
    border: 1px solid #019e42;
}
.TimeInfo .timeBtns .add button:hover{
    background-color: #019e42;
    cursor: pointer;
}

.TimeInfo .timeBtns .summary button,
.TimeInfo .timeBtns .detailed button{
    background-color: #ffffff;
    color: rgb(104, 104, 104);
    border: 1px solid #80808078;
}
.TimeInfo .timeBtns .detailed button:hover,
.TimeInfo .timeBtns .summary button:hover{
    background-color: #e1b170;
    color: white;
    cursor: pointer;
}

.timeBtn{
    padding: 0 5px;
}

.detailed,
.summary{
    padding: 0;
}

.detailed button,
.summary button{
    border-radius: 0 !important;
}

.timeBtn #active-button{
    background-color: #e1b170;
    color: white;
    border: none;
}

#tampered{
    background-color: #c973c9;
}

.timeBtn button{
    /* height: 35px; */
    height: 50px;
    border: none;
    color: white;
    border-radius: 5px;
}

.timeBtn .save{
    background-color: #0062cc;
}
.timeBtn .save:hover{
    background-color: #004c9c;
    cursor: pointer;
}

.TimeInfo .mainData .react-tabs__tab{
    color: #668fca;
}

.TimeInfo .mainData .react-tabs__tab--selected {
    background-color: #668fca;
    color: white;
}

.mainData .react-tabs__tab-list{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    overflow-x: auto;
    overflow-y: hidden;
    font-size: 14px;
}

.table-page{
    position: sticky;
    left: 0;
}

.TimeInfo .mainData .timeTable table{
    font-size: 14px;
}

.timeTable tbody .active {
    background-color: #b1cdff !important;
    color: white;
}

.timeTable tbody .active:hover {
    background-color: #b1cdff;
    color: white;
}

.TimeInfo .mainData .timeTable table thead th{
    z-index: 2;
    background-color: #668fca;
    color: white;
    min-width: 100px;
}

.timeTable  [data-testid="ModeEditIcon"]{
    color: #4ac529;
    border: 1px solid #4ac529;
    border-radius: 5px;
}

.timeTable  [data-testid="ModeEditIcon"]:hover{
    background-color: #4ac529;
    color: white;
    cursor: pointer;
}

.header-filter-holder{
    background-color: #ffd4f7;
    display: flex;
    align-items: center;
    padding: 20px;
    border: 1px solid white;
    font-size: 14px;
}

.filter-header-popUp{
    width: 1200px;
}

.draggable-header:hover{
    cursor: pointer;
}

.header-filter-holder .dragged-item{
    border-radius: 5px;
    padding: 5px;
    margin: 5px;
    background-color: #668fca;
    color: white;
}

.dragged-item .dragged-item-close{
    margin-left: 5px;
}
.dragged-item .dragged-item-close:hover{
    cursor: pointer;
}

.timeTable [data-testid="DeleteForeverIcon"]{
    color: #ff7e7e;
    border: 1px solid #ff7e7e;
    border-radius: 5px;
}

.timeTable [data-testid="DeleteForeverIcon"]:hover{
    background-color: #ff7e7e;
    color: white;
    cursor: pointer;
}

.calendarToggle:hover{
    cursor: pointer;
    color: #668fca;
}

.calendar{
    width: 250px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 10px 10px 10px 10px;
    position: absolute;
    margin-top: 305px;
    margin-right: -5px;
    z-index: 5;
}

#calendar{
    visibility: hidden;
}

#calendar-active{
    visibility: visible;
}

.calendar .react-calendar{border-radius: 10px 10px 10px 10px;}

.react-calendar__navigation{
    background-color: #668fca;
    border-radius: 10px 10px 0 0;
}

.react-calendar__navigation button{color: white;}
.react-calendar__navigation__arrow react-calendar__navigation__prev-button:hover{
    background-color: black;
}

.popUpHolder{
    min-width: 350px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    z-index: 1001;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 57%), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: rgb(104, 104, 104);
    display: none;
}

.popUpHolder .popUp-header{
    padding: 10px 20px;
    align-items: center;
}

.popUp-header .popUp-title{
    width: 80%;
}
.popUp-header .popUp-close{
    width: 20%;
    text-align: right;
}
.popUp-header .popUp-close:hover{
    cursor: pointer;
}

.popUpHolder .popUp-body{
    padding: 20px;
}

.popUpHolder .popUp-body .addTab-item3{
    width: 30%;
}
.popUpHolder .popUp-body .addTab-item,
.popUpHolder .popUp-body .editTab-item{
    padding: 10px 0;
}

.popUpHolder .popUp-footer{
    padding: 10px 20px;
    display: flex;
    align-content: center;
    justify-content: right;
}

option{
    color: rgb(104, 104, 104);
}

select:focus{
    outline: none;
}

@media only screen and (max-width: 1600px){
    .timeTable [data-testid="DeleteForeverIcon"],
    .timeTable  [data-testid="ModeEditIcon"]{
        margin: 5px;
    }
}

@media only screen and (max-width: 900px){
    .uneditable{
        flex-direction: column;
    }

    .popUpHolder .popUp-body .addTab-item3{
        width: 100%;
    }

    .TimeInfo .timeBtns{
        flex-wrap: wrap;
    }
    .timeBtn{
        padding: 5px 5px;
    }
    .processed-post-btns{
        flex-wrap: wrap-reverse !important;
    }
    .summary,
    .detailed{
        padding: 0 !important;
    }
}

@media only screen and (max-width: 1300px){
    .filter-header-popUp{
        width: 1000px;
    }
}

@media only screen and (max-width: 1150px){
    .filter-header-popUp{
        width: 800px;
    }
}

@media only screen and (max-width: 900px){
    .filter-header-popUp{
        width: 500px;
    }

    .timeEntries-search-div{
        flex-direction: column;
    }
}

@media only screen and (max-width: 600px){
    .filter-header-popUp{
        width: 300px;
    }
}