/* Introduction */
.Introduction{
    align-items: center; 
    justify-content: space-between;
    color:rgb(104, 104, 104);
}

.Introduction .dashboard{
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    width: 65%;
    height: 250px;
}

.Introduction .statistics{
    width: 34%;
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    height: 250px;
}

.Introduction .statistics .totalEmployees{
    text-align: center;
    width: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Introduction .statistics .totalEmployees h1{
    margin: 0;
    font-size: 50px;
}

.Introduction .statistics .chart{
    width: 59%;
}

.Introduction .statistics .chart .barStat{
    height: 80%;
}

.Introduction .statistics .chart .barCategory{
    height: 19%;
    align-items: center;
    justify-content: space-evenly;
}

.Introduction .statistics .chart .tooltipCustom{
    background-color: white;
    padding: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}

.Introduction .statistics .chart .barCategory .cat{
    text-align: center;
    width: 33.33%;
    font-size: 14px;
}

.statistics .employee-basic-info{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.statistics .employee-basic-info img{
    border-radius: 10px;
    width: auto;
    height: 70%;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}
.statistics .profile-header{
    padding: 20px;
    background-color: #668fca;
    color: white;
    border-radius: 10px 10px 0 0;
    align-items: center;
}

.events-message{
    margin-bottom: 50px;
    min-height: 100px;
    border: 1px solid #cfcfcf;
    border-radius: 10px;
    padding: 0 20px;
}

.Intro-img{
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.Intro-img img{
    width: 95%;
    /* height: 95%; */
    /* width: 350px; */
    height: 240px;
    position: absolute;
    border: 1px solid #cfcfcf;
}

.imageButton{
    z-index: 100;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: absolute;
}

.imageButton button{
    background-color: #000000b3;
    color: white;
    border: none;
    height: 100px;
}

.imageButton button:hover{
    background-color: #000000dc;
    cursor: pointer;
}

.Intro-content{
    /* padding-left: 20px; */
    overflow: auto;
    width: 59%;
    border: 20px solid white;
}

.Intro-content .contentText{
    padding: 0 20px;
}

.Intro-content p{
    font-size: 18px;
}

.employee{
    width: 100%;
}

.notifications{
    width: 24%;
    padding-bottom: 50px;
    color: rgb(104, 104, 104);
}

.notifications .notifs{
    margin: 50px 20px;
    border-radius: 10px;
    height: 375px;
    width: 100%;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.notifications .notifs .notifHeader{
    height: 50px;
    padding: 0 10px;
    position: sticky;
    top: 0;
    background: #5883ff;
    align-items: center;
    color: white;
    border-radius: 10px 10px 0 0;
}

.notifications .notifs .notifContent{
    overflow: auto;
}

.notifications .notifs .notifContent .notifItem{
    height: 70px;
    margin: 0 20px;
    align-items: center;
    justify-content: center;
    border-bottom: 3px solid rgba(104, 104, 104, 0.3);
}

.notifications .notifs .notifContent .notifItem:hover{
    cursor: pointer;
    background-color: rgba(37, 37, 37, 0.1);
    border-radius: 10px 10px 10px 10px;
}

.notifications .notifs .notifContent .notifItemIcon{
    width: 10%;
}

.notifications .notifs .notifContent .notifItemMsg{
    width: 89%;
    padding: 10px;
}

.notifications .messages{
    margin: 0px 20px;
    border-radius: 10px;
    height: 375px;
    width: 100%;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.notifications .messages .msgHeader {
    height: 50px;
    padding: 0 10px;
    position: sticky;
    top: 0;
    background: #ffa454;
    align-items: center;
    color: white;
    border-radius: 10px 10px 0 0;
}

.notifications .messages .msgContent{
    overflow-y: auto;
}

.notifications .messages .msgContent .msgItem{
    height: 70px;
    padding: 10px;
    margin: 10px 20px;
    align-items: center;
    justify-content: center;
    border-radius: 10px 10px 10px 10px;
    background-color: rgba(37, 37, 37, 0.1);
}

.notifications .messages .msgContent .msgItem .msgItemIcon{
    width: auto;
    height: 80%;
}

.notifications .messages .msgContent .msgItem .msgItemIcon img{
    width: auto;
    height: 100%;
    border-radius: 50%;
}

.notifications .messages .msgContent .msgItem .msgItemMessage{
    width: 79%;
    padding: 10px;
}

.notifications .messages .msgContent .msgItem:hover{
    cursor: pointer;
}

/* Employee Information */
.EmployeeInfo{
    background-color: white;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    border-radius: 10px 10px 10px 10px;
}

.EmployeeList{
    margin: 20px 0;
    height: auto;
    overflow: hidden;
}

.EmployeeList .Header{
    width: 100%;
    height: auto;
    color: rgb(104, 104, 104);
}

.EmployeeList .Header .titleContainer{
    padding: 10px 0 0px 25px;
    align-items: center;
    color: #668fca;
}

.EmployeeSearchBar{
    padding: 0 0 20px 0;
}

.EmployeeSearchBarIcon{
    padding-top: 10px;
    width: 50px;
    text-align: center;
    color: gray;
}
.EmployeeSearchBarInput{
    padding-top: 10px;
    width: 100%;
}

.EmployeeSearchBarInput input{
    height: 100%;
    width: 100%;
    padding: 0;
    border: none;
    border-bottom: 1px solid;
    color: gray;
}

.EmployeeSearchBarInput input:focus{
    outline: none;
}

.EmployeeList .employeeTabs{
    color: rgb(104, 104, 104);
    padding: 10px;
}

.EmployeeList .employeeTabs .react-tabs__tab-list{
    border-bottom: 3px solid rgba(104, 104, 104, 0.3);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
}

.container .Approved{
    background-color: #226d0d;
    color: white;
}
.container .Disapproved{
    background-color: #9e2727;
    color: white;
}
.container .Cancelled{
    background-color: darkgray;
}


.container [data-testid="RemoveRedEyeIcon"]{
    color: #45bdd0;
    border: 1px solid #45bdd0;
    border-radius: 5px;
}

.container [data-testid="RemoveRedEyeIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #45bdd0;
    cursor: pointer;
}
.container [data-testid="ImageIcon"]{
    color: #45bdd0;
    border: 1px solid #45bdd0;
    border-radius: 5px;
}

.container [data-testid="ImageIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #45bdd0;
    cursor: pointer;
}

.emp-transac-manage [data-testid="EditIcon"]{
    color: #4cc92a;
    border: 1px solid #4cc92a;
    border-radius: 5px;
}

.emp-transac-manage [data-testid="EditIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #4cc92a;
    cursor: pointer;
}
.emp-transac-manage [data-testid="NotInterestedIcon"]{
    color: #c52e29;
    border: 1px solid #c52e29;
    border-radius: 5px;
}

.emp-transac-manage [data-testid="NotInterestedIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #c52e29;
    cursor: pointer;
}
.itinerary [data-testid="DeleteForeverIcon"]{
    color: #c52e29;
    border: 1px solid #c52e29;
    border-radius: 5px;
}

.itinerary [data-testid="DeleteForeverIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #c52e29;
    cursor: pointer;
}

.modal-events-image:hover{
    cursor: pointer;
    border: 1px solid #668fca;
}

.top-table{
    padding: 10px 10px;
    width: 100%;
    align-items: center;
}

.top-table .right{
    justify-content: end;
    padding: 0 20px;
}

.top-table .add{
    color: white;
    background-color: #4cc92a;
    border: 1px solid #4e7c1f;
    border-radius: 5px;
    height: 30px;
}

.top-table .add:hover{
    cursor:pointer;
    background-color: #4e7c1f;
}

option{
    font-size: 14px;
}

.EmployeeList .employeeTabs .react-tabs__tab--selected{
    background-color: #668fca;
    color: white;
}

.viewMessage,
.viewNotif{
    min-width: 350px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    z-index: 1001;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 57%), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: rgb(104, 104, 104);
    border-radius: 5px;
    display: none;
}

.viewMessage [data-testid="CloseIcon"]:hover,
.viewNotif [data-testid="CloseIcon"]:hover{
    cursor: pointer;
}

.viewMessage-header,
.viewNotif-header{
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.384);
}

.viewMessage-header .right,
.viewNotif-header .right{
    width: 20%;
    text-align: right;
}

.viewMessage-header .left,
.viewNotif-header .left{
    width: 80%;
}

.viewMessage-content,
.viewNotif-content{
    padding: 20px;
    background-color: #ffedd8;
}

#viewMessage-active,
#viewNotif-active{
    display: block;
}

.viewMessage .viewMessage-body,
.viewNotif .viewNotif-body{
    padding: 20px;
}

.viewMessage-footer,
.viewNotif-footer{
    align-items: center;
    justify-content: end;
}

.viewMessage-footer button,
.viewNotif-footer button{
    border: none;
    color: white;
    border-radius: 3px;
    height: 30px;
}
.viewMessage-footer .cancel,
.viewNotif-footer .cancel{
    background-color: #f33030;
}
.viewMessage-footer .cancel:hover,
.viewNotif-footer .cancel:hover{
    cursor: pointer;
    background-color: #c02222;
}

.viewMessage-footer .save,
.viewNotif-footer .save{
    background-color: #004c9c;
}
.viewMessage-footer .save:hover,
.viewNotif-footer .save:hover{
    cursor: pointer;
    background-color: #053568;
}

@media only screen and (max-width: 1670px){
    /* Introduction */
    .BodySpace{
        width: 79%;
    }
}

@media only screen and (max-width: 1570px){
    /* Introduction */
    .BodySpace{
        width: 75%;
    }
}

@media only screen and (max-width: 1350px){
    /* Introduction */
    .BodySpace{
        width: 70%;
    }

    /* .EmployeeList{
        height: 700px;
    } */

    .homeBody {
        display: flex;
        flex-direction: column;
    }

    .employee {
        width: 100%;
    }
    
    .notifications{
        width: 100%;
    }

    .notifications .notifs, .notifications .messages{
        margin: 10px 0;
        height: 200px;
    }
}

@media only screen and (max-width: 1200px){
    /* Introduction */
    .Introduction{
        display: block;
    }

    .Intro-content{
        padding: 0;
    }

    .Introduction .dashboard {
        width: 100%;
    }

    .Introduction .statistics {
        width: 100%;
        margin-top: 50px;
    }

    .EmployeeInfo{
        width: 100%;
    }

    /* .EmployeeList{
        height: 500px;
    } */
}

@media only screen and (max-width: 1100px){
    /* Introduction */
    .BodySpace{
        width: 65%;
    }
}

@media only screen and (max-width: 1000px){
    /* Introduction */
    .BodySpace{
        width: 63%;
    }
}

/* Mobile */
@media only screen and (max-width: 900px){
    /* Introduction */
    .MainBody {
        display: flex;
        flex-direction: column;
        place-items: center;
    }

    .BodySpace{
        width: 80%;
    }

    .Introduction .dashboard {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 600px;
        width: 100%;
        padding: 10px 0;
    }

    .Introduction .statistics {
        width: 100%;
    }

    .EmployeeInfo{
        width: 100%;
    }

    .Introduction .Intro-img,
    .Introduction .Intro-content{
        width: auto;
        margin-top: 120px;
    }

    .notifications{
        width: 100%;
    }

    .top-table{
        flex-direction: column;
        align-items: start;
    }

    .top-table .right{
        justify-content: start;
        padding: 10px 0 10px 0;
    }

    .statistics-profile{
        height: auto !important;
    }

    .profile{
        padding: 20px 0;
    }
}

@media only screen and (max-width: 600px){
    .profile{
        flex-direction: column;
    }
    
    .statistics .employee-basic-info{
        width: 100%;
    }

    .profile-info{
        padding: 0 20px;
    }
}

.typing-animation span {
    border-right: 2px solid #00000000;
    animation: typing-animation 1s steps(40, end);
    caret-color: transparent;
    color: #668fca;
}

@keyframes typing-animation {
    from {
        width: 0;
    }
}