@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  width: 100%;
  height: 100%;
}

body{
  margin: 0;
  padding: 0;
  background-color: #F1F2FF;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.employeesTab-table table .error {
  background-color: #ce3434;
}

@media only screen and (max-height: 920px){
  .upload-preview-tab{
    max-height: 350px;
    overflow-y: auto;
  }
}

@media only screen and (max-height: 850px){
  .upload-preview-tab{
    max-height: 250px;
    overflow-y: auto;
  }
}
@media only screen and (max-height: 740px){
  .upload-preview-tab{
    max-height: 200px;
    overflow-y: auto;
  }
}
@media only screen and (max-height: 680px){
  .upload-preview-tab{
    max-height: 100px;
    overflow-y: auto;
  }
}

.employeesTab-table table .error td{
  color: white;
}
.employeesTab-table table .error:hover td{
  color: rgb(104, 104, 104);
}

.row{
  display: flex;
  flex-direction: row;
}

.column{
  display: flex;
  flex-direction: column;
}

.MainBody{
  position: absolute;
  margin-top: 100px;
  height:auto;
  width: 100%;
}

.MainBody2 {
  display: flex !important;
  flex-direction: column !important;
  place-items: center !important;
}

.LeftSpace{
  width: 320px;
} 

.BodySpace{
  height: auto;
  width: 80%;
}
.BodySpace2{
  height: auto;
  width: 90% !important;
}

#active-custom-add-form,
#active-custom-edit-form,
#active-custom-delete-form{
    display: block;
}
#custom-add-form,
#custom-edit-form,
#custom-delete-form{
    display: none;
}

.form button{
    height: 35px;
    margin: 0 1px;
    border-radius: 5px;
    border: none;
    font-weight: bold;
}

.form{
  background-color: white;
  min-width: 350px;
  position: absolute;
  left: 50%;
  top: 75%;
  transform: translate(-50%, -50%);
  background-color: white;
  z-index: 1001;
  box-shadow: 0 4px 8px 0 rgb(0 0 0 / 57%), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  color: rgb(104, 104, 104);
  display: none;
}

.modal .popUp-header{
  padding: 10px 20px;
  align-items: center;
}

.modal .popUp-header .popUp-title{
  width: 80%;
}
.modal .popUp-header .popUp-close{
  width: 20%;
  text-align: right;
}
.modal .popUp-header .popUp-close:hover{
  cursor: pointer;
}

.modal .popUp-body{
  min-width: 500px;
  max-height: 600px;
  /* max-height: 500px; */
  padding: 0 20px;
  overflow-y: auto;
}

.modal .popUp-body .popUp-item{
  width: 100%;
}

.modal .popUp-footer{
  padding: 10px 20px;
  display: flex;
  align-content: center;
  justify-content: right;
}

#active-custom-add-form,
#active-custom-edit-form,
#active-custom-view-form,
#active-custom-delete-form{
    display: block;
}
#custom-add-form,
#custom-edit-form,
#custom-view-form,
#custom-delete-form{
    display: none;
}

.modal button{
  border: none;
  height: 34px;
  border-radius: 3px;
}
.modal .save{
  background-color: #2975c5;
  color: white;
}
.modal .save:hover{
  background-color: #1b4d83;
  cursor: pointer;
}
.modal .select{
  background-color: #fd967c;
  color: white;
  border-radius: 3px;
}
.modal .select:hover{
  background-color: #ec7659;
  cursor: pointer;
}
.modal .clear-select{
  background-color: #c52e29;
  color: white;
  border-radius: 3px;
}
.modal .clear-select:hover{
  background-color: #a02521;
  cursor: pointer;
}
.employee-select .select{
  background-color: #fd967c;
  color: white;
  border: none;
  height: 34px;
  border-radius: 3px;
}
.employee-select .select:hover{
  background-color: #ec7659;
  cursor: pointer;
}
.employee-select input::placeholder{
  color: rgba(0, 0, 0, 0.356);
}
.modal .cancel{
  background-color: #c52e29;
  color: white;
}
.modal .cancel:hover{
  background-color: #8d211d;
  cursor: pointer;
}
.modal .add{
  background-color: #4cc92a;
  color: white;
}
.modal .add:hover{
  background-color: #2e9710;
  cursor: pointer;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0s linear 0.3s;
  z-index: 3001;
}

.toggleMessage-overlay{
  background-color: transparent !important;
  pointer-events: none !important;
}
.toggleMessage{
  position: absolute;
  top: 1% !important;
}

.modal-overlay.open {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease, visibility 0s linear 0s;
}

.modal li{
  list-style-type: none;
  padding: 10px;
}

.modal li:hover{
  cursor: pointer;
  background-color: #6690ca8e;
}

.modal {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
  color: rgb(104, 104, 104);
}

.info-item-holder{
  align-items: end;
  justify-content: space-between;
}

.info-item{
  padding: 10px 0;
}

.info-item2{
  width: 50%;
}
.info-item3{
  width: 33%;
}
.info-item4{
  width: 25%;
}

.info-item-label{
  padding: 5px 0;
  font-weight: bold;
}
.info-item input{
  width: 90%;
  color: gray;
  height: 30px;
  border-radius: 3px;
  border: 1px solid #80808080;
}

.info-item input::placeholder{
  color: rgba(0, 0, 0, 0.356);
}

.info-item textarea:focus,
.info-item select:focus,
.info-item input:focus{
  outline: 1px solid #668fca;
}
.info-item select:disabled{
  opacity: 1;
}
.info-item select{
  width: 90%;
  color: rgba(0, 0, 0, 0.356);
  height: 35px;
  border-radius: 3px;
  border-color: #80808080;
}
.info-item select option{
  color: rgb(104, 104, 104);
}

.request_title{
  justify-content: center;
}
.select-employee-table table{
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #00000030;
}
.select-employee-table td{
  text-align: center;
  padding: 10px;
  border-bottom: 1px solid #8080803d;
}
.select-employee-table tbody tr:hover{
  cursor:pointer;
  background-color: #668fca;
  color: white;
}
.select-employee-table tbody .active{
  background-color: #668fca;
  color: white;
}
.select-employee-table th{
  background-color: #bacde757;
  color: rgb(104, 104, 104);
  padding: 10px;
  min-width: 100px;
}
.table-page button{
  border: none;
  color: white;
}
.table-page .prev{
  background-color: #ed8b2c;
}
.table-page .next{
  background-color: #ed8b2c;
}
.table-page .prev:hover{
  background-color: #c46302;
  cursor: pointer;
}
.table-page .next:hover{
  background-color: #c46302;
  cursor: pointer;
}
.restriction{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  background-color: #f76300;
  padding: 20px;
  color: white;
  border-radius: 5px;
  
}

.warning-message{
  top: 125px !important;
}

/* Scrollbar container */
::-webkit-scrollbar {
  width: 8px;
}

/* Scrollbar track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Scrollbar handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 6px;
}

/* Scrollbar handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #777;
}

.custom-loader {
  width: 120px;
  height: 22px;
  border-radius: 20px;
  border: 2px solid #668fca;
  position: relative;
  overflow: hidden;
}

.custom-loader::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  background: #668fca;
  animation: loading-animation 2s infinite;
}


@media only screen and (max-width: 1200px){
  .modal .popUp-body{
    /* max-height: 360px; */
    max-height: 500px;
  }
}

@media only screen and (max-width: 900px){
  .info-item{
      width: 100%;
  }

  .info-item-holder{
      flex-direction: column;
  }

  .modal .popUp-body{
    min-width: 300px;
  }
}

@media only screen and (max-height: 800px){
  .modal .popUp-body{
    max-height: 400px !important;
  }
}

@media only screen and (max-height: 600px){
  .modal .popUp-body{
    max-height: 300px !important;
  }
}