.EmployeesTabInfo{
    width: 100%;
    background-color: white;
    border-radius: 10px 10px 10px 10px;
    /* box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
    color: rgb(104, 104, 104);
    margin: 0 0 50px 0;
}

.add-employees-form button,
.employeesTab-body .body-top-btn button{
    height: 35px;
    margin: 5px;
    border-radius: 5px;
}

.employeesTab-tabPanel{
    height: 700px;
    padding: 20px;
}

.add-employeesTab-tabPanel{
    height: 700px;
    padding: 20px;
}

.sub-title{
    align-items: center;
    font-size: 13px
}

.employeesTab-title{
    padding: 20px 0;
}

.EmployeesTabInfo .employeesTab-header{
    padding: 5px 20px;
}

.EmployeesTabInfo .employeesTab-header h3{
    margin-bottom: 0;
    color: #668fca;
}

.employeesTab-body{
    padding: 10px 20px;
}

.view-emp{
    /* max-height: 600px; */
    overflow: auto;
}

.generate-submit{
    color: white;
    background-color: #0062cc;
    border: none;
    height: 34px;
    border-radius: 3px;
}
.generate-submit:hover{
    cursor: pointer;
    background-color: #06468a;
}

.print-button{
    color: white;
    background-color: #f8a960;
    border: none;
    height: 34px;
    border-radius: 3px;
}
.print-button:hover{
    cursor: pointer;
    background-color: #c58548;
}

.employeesTab-tab .react-tabs__tab-list{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    overflow-x: auto;
    overflow-y: hidden;
    font-size: 14px;

    position: sticky;
    top: 0;
    background-color: white;
}

.employeesTab-tab .react-tabs__tab--selected{
    background-color: #668fca;
    color: white;
}

.personal-tab .major-info .major-info-left{
    width: 30%;
    padding: 10px 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.personal-tab .major-info .major-info-left img{
    /* border-radius: 50%; */
    /* border: 1px solid gray; */
    max-width: 200px;
    height: 200px;
}
.personal-tab .major-info .major-info-left img:hover{
    cursor: pointer;
    border: 3px solid #668fca;
}

.personal-tab .major-info .major-info-right{
    width: 70%;
    padding: 10px 0;
}

.major-info-right-item{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.tab-footer{
    width: 100%;
}

.tab-footer .buttons{
    align-items: center;
    justify-content: end;
    padding: 20px 0;
}

button{
    align-items: center;
    justify-content: center;
}

.lowerpart .submit{
    border-radius: 5px;
    height: 50px;
    width: 100px;
    border: none;
    color: white;
    background-color: #0062cc;
}
.lowerpart .submit:hover{
    cursor: pointer;
    color: white;
    background-color: #05468b;
}
.tab-footer .buttons .next{
    border: none;
    color: white;
    background-color: rgb(253, 172, 78);
}
.tab-footer .buttons .next:hover{
    cursor: pointer;
    color: white;
    background-color: rgb(187, 122, 47);
}
.tab-footer .buttons .previous{
    border: none;
    color: white;
    background-color: rgb(253, 78, 78);
}
.tab-footer .buttons .previous:hover{
    cursor: pointer;
    color: white;
    background-color: rgb(163, 45, 45);
}

