}
.tab-footer .save{
    border: none;
    color: white;
    background-color: #2371e6;
    height: 35px;
    margin: 0 1px;
    border-radius: 5px;
    font-weight: bold;
}
.tab-footer .save:hover{
    cursor: pointer;
    color: white;
    background-color: #3b9c21;
}

.centeredStyle {
    display: flex;
    align-items: center;
    justify-content: center;
}

.id-container{
    border: 1px solid black;
    height: 600px;
    margin: 5px;
    width: 48%;
    position: relative;
}

.id-container img {
    width: 100%; /* Stretch the image to 100% of container width */
    height: 100%; /* Stretch the image to 100% of container height */
}

.employeesTab-table table{
    font-size: 14px;
}

.employeesTab-table tbody .active {
    background-color: #b1cdff;
    color: white;
}

.employeesTab-table tbody .active:hover {
    background-color: #b1cdff;
    color: white;
}

.view-employee{
    padding: 0 !important;
}

.view-employee-header{
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 1;
}

.add-employees-form{
    background-color: white;
    min-width: 350px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    z-index: 1001;
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 57%), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    color: rgb(104, 104, 104);
    /* display: none; */
}

.add-employees-form .popUp-header{
    padding: 10px 20px;
    align-items: center;
}

.add-employees-form .popUp-header .popUp-title{
    width: 80%;
}
.add-employees-form .popUp-header .popUp-close{
    width: 20%;
    text-align: right;
}
.add-employees-form .popUp-header .popUp-close:hover{
    cursor: pointer;
}

.add-employees-form .popUp-body{
    padding: 0 20px
}

.add-employees-form .popUp-body .popUp-item{
    width: 100%;
}

.add-employees-form .popUp-footer{
    padding: 10px 20px;
    display: flex;
    align-content: center;
    justify-content: right;
}

#previous-button{
    visibility: hidden;
}
#next-button{
    visibility: hidden;
}
#active-previous-button{
    visibility: visible;
}
#active-next-button{
    visibility: visible;
}

.body-top{padding: 10px 0;}

.body-top-search{
    width: 50%;
}

.body-top-btn{
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
    /* justify-content: end; */
}

.employeesTab-table  [data-testid="ModeEditIcon"]{
    color: #4ac529;
    border: 1px solid #4ac529;
    border-radius: 5px;
}

.employeesTab-table  [data-testid="ModeEditIcon"]:hover{
    transform: scale(1.1);
    color: white;
    background-color: #4ac529;
    cursor: pointer;
}
