.EmployeesTabInfo .employeesTab-header button{
    background-color: #0062cc;
    color: white;
    border: none;
    border-radius: 3px;
    padding: 5px;
}

.EmployeesTabInfo .employeesTab-header button:hover{
    background-color: #004c9c;
    cursor: pointer;
}

.add-disabled{
    background-color: #bebebe;
    cursor: not-allowed;
}

@media only screen and (max-width: 1200px){
    .major-info{
        flex-direction: column;
    }
    .major-info,
    .minor-info{
        width: 100%;
    }
    .personal-tab .major-info .major-info-left, 
    .personal-tab .major-info .major-info-right{
        width: 100%;
    }

    .view-employee{
        max-height: 600px;
        overflow-y: auto;
    }
}

@media only screen and (max-width: 900px){
    .employee-body-top{
        flex-direction: column;
    }

    .employee-body-top .body-top-btn{
        justify-content: start;
        padding: 10px 0 0 0;
    }
}

@media only screen and (max-width: 700px){
    .ma-filter-sect{
        flex-direction: column;
    }
}
@media only screen and (max-width: 900px){
    .generate-id-body-container{
        flex-direction: column;
    }

    .id-container{
        width: 108%;
    }
}
@media only screen and (max-height: 1000px){
    .employeesTab-tabPanel{
        height: 500px;
    }
}
@media only screen and (max-height: 900px){
    .employeesTab-tabPanel{
        height: 300px;
    }
}